(function() {
  'use strict';

  angular
    .module('site', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('site')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, toastr, ContributionsService) {
    var vm = this;

    vm.message = 'Welcome';
    vm.contributions = ContributionsService.all();

    vm.showToastr = function () {
      toastr.info('Watchout!');
      vm.classAnimation = '';
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('site')
    .factory('ContributionsService', ContributionsService);

  /** @ngInject */
  function ContributionsService() {
    var _contributions = [
      {
        name:"koa-mongo-api",
        url:"https://github.com/harin76/koa-mongo-api",
        description:"A boilerplate for writing restful API's in koajs and mongodb"
      }
    ];

    var service = {
      all : all
    }

    return service;

    function all () {
      return _contributions;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('site')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('site')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('site')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('site')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("site").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div class=\"jumbotron\" ng-repeat=\"contrib in main.contributions\"><h2 class=\"text-primary\"><i class=\"ion-flag\"></i> {{contrib.name}}</h2><p></p><p>{{contrib.description}}</p><a href=\"{{contrib.url}}\"><i class=\"ion-social-github\"></i> {{contrib.url}}</a><p></p></div><p class=\"text-muted\">Site Generated by awesome <a href=\"https://github.com/Swiip/generator-gulp-angular\">generator gulp angular</a> and powered by <strong>Github Pages <strong></strong></strong></p></div>");}]);